.product-card {
  width: 200px;
  height: auto;
  border: 1px solid rgba(243, 243, 243, 1);
  border-radius: 5px;
  padding: 32px 16px 16px;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  &__img {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    color: $title-text-colorl;
    margin: 0 auto 4px;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
  }

  &__code {
    font-size: 10px;
    font-weight: 400;
    color: $text-color;
    margin-bottom: 17px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__review-text {
    color: $title-text-colorl;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    align-self: end;
  }

  &__value {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }

  &__price-text {
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    color: $text-color;
  }

  &__price {
    color: $title-text-colorl;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    width: 100%;
    height: 40px;
    background-color: rgba(0, 172, 220, 1);
    color: $background-color;
    border-radius: 5px;
    border: 1px solid rgba(0, 172, 220, 1);
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    padding: 0;
    transition: all 0.3s;

    &:hover {
      background-color: #fff;
      color: #00acdc;
    }
  }
}

.card:hover .card__title {
  color: #34568b;
}
